import React from "react";
import {TwitterPicker} from "react-color"

const Palette = ({ inputs, setInputs }) => {
  const {backgroundColor} = inputs
  const paletteWrapper = {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    margin: "5px"
  };
  const handleChangeComplete = (color) => {
    console.log(color)
    setInputs({
      ...inputs,
      backgroundColor: color.hex
    });

  }
  return (
    <div className="paletteWrapper" style={paletteWrapper}>
        <TwitterPicker color={backgroundColor} onChangeComplete={handleChangeComplete} triangle="hide" width="100%"></TwitterPicker>
    </div>
  );
};

export default Palette;