import React from 'react';
import { Modal, Button, Upload, message} from 'antd';
import { useState, useRef, useEffect } from 'react';



function beforeUpload(file) {
  const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
  if (!isJpgOrPng) {
    message.error('JPG와 PNG만 가능합니다')
  }  
  return isJpgOrPng
}

function setPhoto(blob, setInputs, inputs, selectedPhoto) {
    let reader = new FileReader();
    reader.readAsDataURL(blob);
    reader.onload = () => {
      selectedPhoto.current = reader.result;
      setInputs({
        ...inputs,
        img64: reader.result,
        scale: 60,
        imgsize: 100,
        imageToggle: true
      });
    };

}
const ImageUpload = ({inputs, setInputs}) => {
  const selectedPhoto = useRef(null);
  const handleUploadButton = e => {
    setInputs({
      ...inputs,
      uploadVisible: e
    });
  };
  const dummyRequest = ({ file, onSuccess }) => {
    setTimeout(() => {
        onSuccess("ok");
      }, 0);
      selectedPhoto.current = file;
      setPhoto(selectedPhoto.current, setInputs, inputs, selectedPhoto)
    };
  const [state, setstate] = useState({
      selectedFile: null,
      selectedFileList: []
  })
  const imgUploadButton = useRef()
    const onChange = info => {
        const nextState = {};
        switch (info.file.status) {
          case "uploading":
            nextState.selectedFileList = [info.file];
            break;
          case "done":
            nextState.selectedFile = info.file;
            nextState.selectedFileList = [info.file];
            break;
    
          default:
            // error or removed
            nextState.selectedFile = null;
            nextState.selectedFileList = [];
        }
        setstate(() => nextState);

      };
      useEffect(() => {
        if (inputs.imgExist) {

 
            let i=document.getElementsByTagName('input')
            i[2].click()
        }

      }, [inputs.imgExist]);
// const setPhoto = (blob) => {
//     selectedPhoto.current = blob;
//     setImage(selectedPhoto.current)
// }

// const handleOk = e => {
//     console.log(selectedPhoto.current)
//     if (selectedPhoto.current !== null){
//  //       setImage(selectedPhoto.current)
        
//     }
  
//      onClick(false)
// }

// const handleCancel = e => {
//     onClick(false)
// }
return (
    <div style={{padding:"10px"}}>
        {/* <Modal title="이미지 업로드" visible={visible} onOk={handleOk} onCancel={handleCancel}> */}
        <Upload
          fileList={state.selectedFileList}
          customRequest={dummyRequest}
          onChange={onChange}
          showUploadList={false}
          beforeUpload={beforeUpload}
        >
          <Button ref={imgUploadButton}>Choose File</Button>
        </Upload>
            {/* <input type="file" name="file" onChange={e => console.log(e.target.files[0])}/> */}
        {/* </Modal> */}
    </div>
)

}

export default ImageUpload