import { Drawer, Button, Radio, List, Card } from 'antd';
import React from 'react';
const data = [
    {
        title: '롱쉐도우 : 녹색',
        class: 'textStyle_1'
    },
    {
        title: '아웃라인',
        class: 'textStyle_2'
    },
    {
        title: '카툰풍 글씨',
        class: 'textStyle_3'
    },
    {
        title: '희미한 그림자',
        class: 'textStyle_4'
    },
    {
        title: '롱쉐도우 : 블랙',
        class: 'textStyle_5'
    },
    {
        title: '흰색 투명',
        class: 'textStyle_6'
    },
    {
        title: '인터뷰 형',
        class: 'textStyle_7'
    },
    {
        title: '스트라이프 그라데이션',
        class: 'textStyle_8'
    }
]

const PresetMenu = ({inputs, setInputs}) => {
    const closeDrawer = e => {
        setInputs({
            ...inputs,
            drawer: false
        })
    }
    const setStyle = e => {
        console.log(e.target.className)
        setInputs({
            ...inputs,
            textStyle: e.target.className
        })
    }
    return (
        <>
            <Drawer title="프리셋 선택" visible={inputs.drawer} onClose={closeDrawer} placement="bottom" mask={false} height="350">
                <List grid={{column: 2, gutter: 16}} dataSource={data} renderItem={item => (
                    <List.Item>
                        <Card title={item.title} bodyStyle={{padding:'6px'}}><div style={{overflow:'hidden', height:'60px', textOverflow:'ellipsis'}}><span className={item.class} onClick={setStyle} style={{fontSize: '2.5rem'}}>안녕하세요</span></div></Card>
                    </List.Item>)} />
                
            </Drawer>
        </>
    )
}

export default PresetMenu

