
import {
  WingBlank,
  Slider,
  Flex,
  WhiteSpace,
  List,
  Switch
} from "antd-mobile";
import React, { useEffect, useRef, useState } from "react";

const SizeSlider = ({ inputs, setInputs }) => {
  const {
    uploadVisible,
    scale,
    drag,
    html,
    img64,
    imgsize,
    backgroundColor,
    drawer,
    imageToggle,
    imgExist
  } = inputs;

  const handleScale = e => {
    setInputs({
      ...inputs,
      scale: e
    });
  };
  const handleImageSize = e => {
    setInputs({
      ...inputs,
      imgsize: e
    });
  };
  useEffect(() => {
    console.log(imageToggle)
  }, [imageToggle]);
  return (
    <>
        <WingBlank size="md">
         
        <List renderHeader={() => '이미지 사이즈 조절'} className="my-list"></List>
              {/* <WhiteSpace size="md" ></WhiteSpace> */}
              <List.Item
          extra={<Switch
            checked={imageToggle}
            onChange={() => { 
              if (!imgExist && !imageToggle){
                console.log(imgExist)
                setInputs({
                  ...inputs,
                  imageToggle : !imageToggle,
                  imgExist: true
                })

              }
              else{
            setInputs({
              ...inputs,
              imageToggle : !imageToggle
            })}
          }
}
          />}
        >이미지 사용</List.Item>
          <List.Item>
            <Slider
            style={{ marginLeft: 30, marginRight: 30 }}
            defaultValue={100}
            min={0}
            max={200}
            onChange={handleImageSize}
          /></List.Item>


          </WingBlank>


    </>
  );
};

export default SizeSlider;
