import React, { useEffect, useRef, useState } from "react";
import ContentEditable from "react-contenteditable";
import Draggable from "react-draggable";
import domtoimage from "dom-to-image";
import { saveAs } from "file-saver";
import {
  Col,
  InputNumber,
  Layout,
  Menu,
  Row,
  Slider,
  Typography,
  Drawer
} from "antd";
const { Text } = Typography;

const Preview = ({ inputs, setInputs }) => {
  const {
    uploadVisible,
    scale,
    drag,
    html,
    img64,
    imgsize,
    backgroundColor,
    drawer,
    textStyle,
    save,
    scaleChange,
    imageToggle
  } = inputs;
  const htmlRef = useRef(html);

  const canvasRef = useRef();

  const handleEditable = _e => {
    setInputs({
      ...inputs,
      drag: true
    });
  };

  const handleEditorBlur = e => {
    setInputs({
      ...inputs,
      html:
        htmlRef.current.innerHTML === ""
          ? "입력하세요."
          : htmlRef.current.innerHTML,
      drag: false
    });
  };

  const handleText = e => {
    setInputs({
      ...inputs,
      html: e.target.value
    });
  };
  useEffect(() => {
    // htmlRef.current.innerHTML = html;
    if (drag) {
      htmlRef.current.focus();
    }

    if (save) {
      setInputs({
        ...inputs,
        scale: `scale(1)`,
        scaleChange: true
      });
      console.log(scale);
      return;
    }
  }, [html, drawer, drag, save]);
  useEffect(() => {
    if (scaleChange) {
      console.log(scale);
      setTimeout(
        domtoimage.toBlob(canvasRef.current).then(function(blob) {
          window.saveAs(blob, "Illywa_thumnail.png");
          setInputs({
            ...inputs,
            scale: `scale(${window.innerWidth / 1280})`,
            scaleChange: false,
            save: false
          });
        }),
        1000
      );

      return;
    }
  }, [scaleChange]);

  return (
    <div
      className="Wrapper"
      style={{
        display: "block",
        height: `${(window.innerWidth * 720) / 1280}px`
      }}
    >
      <div
        className="PreviewScreen"
        name="canvas"
        ref={canvasRef}
        style={{
          width: "1280px",
          height: "720px",
          backgroundColor: backgroundColor,
          position: "relative",
          overflow: "hidden",
          transform: scale,
          transformOrigin: "top left"
        }}
      >
        {imageToggle !== false ? (
          <img
            // ref={selectedPhoto}
            src={img64}
            style={{
              width: `${imgsize}%`,
              left: "50%",
              top: "50%",
              transform: "translate(-50%,-50%)",
              position: "absolute"
            }}
          ></img>
        ) : (
          ""
        )}
        <Draggable disabled={drag} bounds="parent">
          <div
            className="test"
            style={{
              right: "35px",
              bottom: "10px",
              position: "absolute"
            }}
            onDoubleClick={handleEditable}
          >
            <div style={{ display: drag ? "block" : "none" }}>
              <Text code>글씨 수정 가능</Text>
            </div>
            <ContentEditable
              className={`TextArea ${textStyle !== "" ? textStyle : ""}`}
              innerRef={htmlRef}
              disabled={!drag}
              html={html}
              onChange={handleText}
              onBlur={handleEditorBlur}
            />
          </div>
        </Draggable>
      </div>
    </div>
  );
};

export default Preview;
