import { UploadOutlined, FormOutlined, DownloadOutlined } from "@ant-design/icons";
import React, { useEffect, useRef, useState } from "react";
import 'antd-mobile/dist/antd-mobile.css';
import "./App.css";
import Palette from "./components/Palette/Palette_mobile";
import ImageUpload from "./components/upload/imageUpload";
import { Button, NavBar, Icon, Flex, WingBlank, WhiteSpace, InputItem } from 'antd-mobile';
import PresetMenu from "./components/presetMenu/presetMenu_mobile";
import Preview from "./components/preview/Preview_mobile";
import SizeSlider from "./components/slider/SizeSlider_mobile";

// antd 비구조화 할당

function App() {
  const [inputs, setInputs] = useState({
    uploadVisible: false,
    scale: `scale(${window.innerWidth / 1280})`,
    drag: false,
    html: "썸네일 제작기",
    img64: "",
    imgsize: 100,
    imageToggle: false,
    backgroundColor: "#697689",
    drawer: false,
    textStyle: "textStyle_Default",
    save: false,
    scaleChange: false,
    imgExist: false
  });

  const {
    uploadVisible,
    scale,
    drag,
    html,
    img64,
    imgsize,
    backgroundColor,
    drawer,
    save,
    imageToggle,
    imgExist
  } = inputs;

  const handleDrawer = e => {

    setInputs({
      ...inputs,
      drawer: !drawer
    });
  };

  const handleSave = e => {

    setInputs({
      ...inputs,
      save: !save
    });
  };

  const handleText = e => {
    console.log(e)
    setInputs({
      ...inputs,
      html: e
    });
  };

  return (
    <>



  <div>
    <NavBar
      mode="light"
      icon={<Icon type="left" />}
      onLeftClick={() => console.log('onLeftClick')}
      rightContent={[
        <FormOutlined style={{ marginRight: '24px' }} onClick={handleDrawer}/>,
        <DownloadOutlined onClick={handleSave}/>,
      ]}
    >일리와 썸네일 제작</NavBar>

    <Preview inputs={inputs} setInputs={setInputs}></Preview>
    <WhiteSpace size="lg"></WhiteSpace>
    <InputItem type="text" value={html} placeholder="여기에 입력하세요" onChange={handleText}>텍스트</InputItem>
    <WhiteSpace size="lg"></WhiteSpace>
      <SizeSlider inputs={inputs} setInputs={setInputs}></SizeSlider>
      <WhiteSpace size="xl"></WhiteSpace>
      <WhiteSpace size="xl"></WhiteSpace>
      <WingBlank size="md">
        <Flex>이미지 업로드</Flex>

        <ImageUpload inputs={inputs} setInputs={setInputs} />
      </WingBlank>
      <WingBlank size="md">
        <Flex>배경색 선택</Flex>
        <WhiteSpace size="md"></WhiteSpace>
        <Palette inputs={inputs} setInputs={setInputs} />
      </WingBlank>
      <PresetMenu  inputs={inputs} setInputs={setInputs}/>
  </div>

    </>
  );
}

export default App;
